import save from '@/static/mbcImg/publish/purchase/save.png';
import preview from '@/static/mbcImg/publish/purchase/preview.png';
import publish from '@/static/mbcImg/publish/purchase/publish.png';
import fixedBgBtn from '@/components/common/button/fixedBgBtn.vue';
import savePreviewBtn from '@/components/common/button/savePreviewBtn.vue';
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			save: save,
			preview: preview,
			publish: publish
		};
	},
	components: {
		fixedBgBtn,
		savePreviewBtn
	},
	computed: {
		i18n() {
			return this.$t('navigation');
		},
		...mapGetters(['GET_PUBLISH'])
	},
	watch: {
		GET_PUBLISH: {
			handler(a, b) {
			},
			deep: true
		}
	},
	methods: {
		...mapMutations({
			setPreviceCaiGou: 'setPreviceCaiGou'
		}),
		caiGouSavePublish (e) {
			console.log('触发保存发布按钮');
			let params = this.GET_PUBLISH.publishPurchase.caiGouList;
			let listData = this.GET_PUBLISH.publishPurchase.imgList.imageData;
			if (params.purcTitle === '') {
				uni.showToast({
					title: '请填写采购名称！',
					icon: 'none',
					duration: 1000
				});
				return
			}
			if (!uni.getStorageSync('landRegist')) {
				this.landRegistra(); // 判断登录状态
			} else if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				params.purcState = e; // 状态
				if (listData) { // 附件
					listData.map((items, index) => {
						params.imgList.push(items.imgName)
					})
				};
				params.userId = landRegistLG.user.id; // id
				params.purcType = String(this.GET_PUBLISH.publishPurchase.templetType);// 简单模板 // 普通模板
				delete params.purcCcodeStr; // 删除地址text字段
				uni.request({
					url: this.api2 + '/rest-rp/purc/add', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						if (response.data.ret === '200') {
							uni.hideLoading(); // 隐藏 loading
							if (e === '0') { // 保存
								uni.showToast({
									title: '保存成功！',
									icon: 'none',
									duration: 1000
								});
							} else if (e === '1') { // 发布
								uni.showToast({
									title: '发布成功！',
									icon: 'none',
									duration: 1000
								});
							}
						} else {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络开小差了，请稍后重试',
								icon: 'none',
								duration: 1000
							});
						}
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		caiGouPreview () {
			console.log('预览采购填写的内容');
			let caiGouList = this.GET_PUBLISH.publishPurchase.caiGouList;
			if (caiGouList.purcTitle === '') {
				uni.showToast({
					title: '请填写采购名称！',
					icon: 'none',
					duration: 1000
				});
				return
			}
			this.$store.commit('setPreviceCaiGou', true); // 更新setPreviceCaiGou
		}
	}
};
